body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Container{
    position: absolute;
    margin-top: 120px;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    min-height: 450px;
}

.center {
    position: absolute;
    width: 100px;
    height: 50px;
    top: 50%;
    left: 50%;
    margin-left: -50px; /* margin is -0.5 * dimension */
    margin-top: -100px; 
  }
.login-div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    min-height: 60%;
    margin-top: 20px;
  }

.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 340px;
    height: auto;
    padding: 5px;
    box-sizing: border-box;
  }
  
  .login {
    position: relative;
    width: 100%;
    padding: 10px;
    margin: 0 0 10px 0;
    box-sizing: border-box;
    border-radius: 3px;
    background: #FAFAFA;
    overflow: hidden;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
  }
  
  .login > header {
    position: relative;
    width: 100%;
    padding: 10px;
    margin: -10px -10px 25px -10px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    background:  rgb(118, 116, 243);
    font-family: 'Roboto', sans-serif;
    font-size: 1.3rem;
    color: #FAFAFA;
    box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.14),
                0px 1px 5px 0px rgba(0, 0, 0, 0.12),
                0px 3px 1px -2px rgba(0, 0, 0, 0.2);
  }
  
  .login > header:before {
    content: '';
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100%;
    height: 5px;
    padding: 10px;
    margin: -10px 0 0 -10px;
    box-sizing: border-box;
    background: rgba(0, 0, 0, 0.156);
    font-family: 'Roboto', sans-serif;
    font-size: 0.9rem;
    color: transparent;
    z-index: 5;
  }
  
  .login > header h4 {
    font-size: 0.7em;
    -webkit-animation: text_opacity 1.5s cubic-bezier(.55, 0, .1, 1);
            animation: text_opacity 1.5s cubic-bezier(.55, 0, .1, 1);
    color: rgba(255, 255, 255, 0.7);
  }
  
  /* Form */
  .login-form {
    padding: 15px;
    box-sizing: border-box;
  }
  
  
  /* Inputs */
  .login-input {
    position: relative;
    width: 100%;
    padding: 10px 5px;
    margin: 0 0 25px 0;
    border: none;
    border-bottom: 2px solid rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
    background: transparent;
    font-size: 1rem;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    opacity: 1;
    -webkit-animation: input_opacity 0.8s cubic-bezier(.55, 0, .1, 1);
            animation: input_opacity 0.8s cubic-bezier(.55, 0, .1, 1);
    -webkit-transition: border-bottom 0.2s cubic-bezier(.55, 0, .1, 1);
    transition: border-bottom 0.2s cubic-bezier(.55, 0, .1, 1);
  }
  
  .login-input:focus {
    outline: none;
    border-bottom: 2px solid #3700B3;
  }
  
  
  /* Submit Button */
  .submit-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    position: relative;
    padding: 10px;
    margin: 35px -25px -25px -25px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }
  
  .login-button {
    padding: 10px;
    border: none;
    border-radius: 3px;
    background: transparent;
    font-family: 'Roboto', sans-serif;
    font-size: 0.9rem;
    font-weight: 500;
    color: #3700B3;
    cursor: pointer;
    opacity: 1;
    -webkit-animation: input_opacity 0.8s cubic-bezier(.55, 0, .1, 1);
            animation: input_opacity 0.8s cubic-bezier(.55, 0, .1, 1);
    -webkit-transition: background 0.2s ease-in-out;
    transition: background 0.2s ease-in-out;
  }
  
  .login-button:hover {
    background: rgba(0, 0, 0, 0.05);
  }
  
.App {
  /*background-color: brown;*/
  text-align: center;
  font-family: 'Roboto';
  margin-top: 0px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

